img {
  display: block;
  width: 100%;
}

a {
  font-weight: 300;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

body {
  background-color: #f2f2f2;
}

.wrapper {
  max-width: 1920px;
  max-width: 120rem;
  margin: 0 auto;
}

.page-content {
  position: absolute;
  top: 176px;
  top: 11rem;
  left: 0;
  width: 100%;
  height: calc(100vh - 11rem);
  padding: 0 32px;
  padding: 0 2rem;
}

@media (min-width: 960px) {
  .page-content {
    position: relative;
    top: 6rem;
    padding-left: 6rem;
  }
}

@media screen and (min-width: 640px) {
  .page-content .intro {
    position: relative;
    margin: 0 -2rem;
    width: calc(100% + 4rem);
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  }
}

@media (min-width: 960px) {
  .page-content .intro {
    height: 30rem;
    margin: 0 0 0 -3rem;
    width: calc(100% + 3rem);
  }
  .page-content .intro .banner {
    left: 0;
    width: 100%;
    background-position-y: 35%;
  }
}

.main-content {
  padding: 0 0 80px 0;
  padding: 0 0 5rem 0;
}

@media (min-width: 960px) {
  .main-content {
    padding-left: 30rem;
  }
}

footer {
  padding-bottom: 80px;
  padding-bottom: 5rem;
  text-align: center;
}

footer p {
  font-size: 22.4px;
  font-size: 1.4rem;
  font-weight: 300;
  color: #999999;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  height: 176px;
  height: 11rem;
  width: 100%;
  background-image: url("/assets/mobile-header-bg.jpg");
  background-repeat: repeat-x;
  background-size: auto 100%;
  z-index: 997;
}

@media (min-width: 960px) {
  header {
    position: fixed;
    background-color: #ffffff;
    background-image: none;
    height: 6rem;
    border-bottom: 0.4rem solid #ff4040;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  }
}

.logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-65%, -50%);
  width: 320px;
  width: 20rem;
  z-index: 999;
}

@media (min-width: 960px) {
  .logo {
    top: 0;
    left: 3rem;
    transform: none;
    width: 30rem;
    height: 37rem;
    background-image: url("/assets/desktop-logo-bg.jpg");
    background-size: auto 100%;
    background-position: bottom center;
  }
}

.logo .horizontal {
  display: block;
}

.logo .horizontal svg {
  width: 100%;
}

.logo .vertical {
  display: none;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
}

.logo .vertical svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: calc(100% - 12rem);
}

.logo.desktop {
  display: none;
  top: -112px;
  top: -7rem;
}

@media (min-width: 960px) {
  .logo.desktop {
    display: block;
  }
  .logo.mobile {
    display: none;
  }
  .logo .horizontal {
    display: none;
  }
  .logo .vertical {
    display: block;
  }
}

body,
body * {
  font-family: 'Roboto', Arial, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
}

h1 {
  color: #ff4040;
  font-size: 32px;
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 12px;
  margin-bottom: .75rem;
}

h2 {
  color: #000000;
  font-size: 32px;
  font-size: 2rem;
  font-weight: 500;
}

h3 {
  color: #000000;
  font-size: 25.6px;
  font-size: 1.6rem;
  font-weight: 500;
}

h4 {
  color: rgba(0, 0, 0, 0.6);
  font-size: 19.2px;
  font-size: 1.2rem;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 0;
}

p {
  color: #000000;
  font-size: 22.4px;
  font-size: 1.4rem;
  font-weight: 300;
  line-height: 38.4px;
  line-height: 2.4rem;
  margin: 0;
}

.banner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 0;
  background-size: cover;
  background-position: center center;
}

.banner:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 160px;
  height: 10rem;
  background: linear-gradient(to bottom, rgba(242, 242, 242, 0) 0%, #f2f2f2 100%);
}

@media screen and (min-width: 640px) {
  .banner {
    left: 30rem;
    width: calc(100% - 30rem);
    height: 100%;
  }
  .banner img, .banner:after {
    display: none;
  }
}

body.component {
  padding: 96px;
  padding: 6rem;
}

.info {
  display: block;
  position: relative;
  width: 100%;
  max-width: 640px;
  max-width: 40rem;
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  padding: 16px 48px 0 48px;
  padding: 1rem 3rem 0 3rem;
  margin: 65vw auto 0 auto;
  z-index: 996;
}

@media screen and (min-width: 640px) {
  .info {
    margin: 0;
    width: 30rem;
    max-width: none;
    box-shadow: none;
  }
}

@media (min-width: 960px) {
  .info {
    top: 30rem;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
    z-index: 998;
  }
}

.info section {
  padding: 24px 0;
  padding: 1.5rem 0;
}

.info section:not(:last-child) {
  border-bottom: 1px solid #f2f2f2;
}

.info table {
  border-spacing: 0;
  border-collapse: separate;
  margin-bottom: 16px;
  margin-bottom: 1rem;
}

.info table td {
  font-size: 22.4px;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 38.4px;
  line-height: 2.4rem;
  padding: 0;
}

.info table td:first-child {
  width: 144px;
  width: 9rem;
}

.info table.contact td:last-child {
  font-weight: 300;
}

.info table a.mailto {
  color: #000000;
  text-decoration: underline;
}

.info .more-link {
  display: block;
  text-align: center;
  color: #ff4040;
  font-weight: 500;
  font-size: 22.4px;
  font-size: 1.4rem;
  padding: 32px 0;
  padding: 2rem 0;
  margin: 0 -48px;
  margin: 0 -3rem;
}

nav {
  display: none;
  position: relative;
  max-width: 1920px;
  max-width: 120rem;
  margin: 0 auto;
  height: 96px;
  height: 6rem;
}

@media (min-width: 960px) {
  nav {
    display: block;
  }
}

nav ul {
  position: absolute;
  top: 50%;
  right: 32px;
  right: 2rem;
  list-style: none;
  transform: translateY(-50%);
  padding: 0;
  margin: 0;
}

nav ul li {
  display: inline-block;
  margin-left: 32px;
  margin-left: 2rem;
}

nav ul li a {
  color: #000000;
  font-size: 22.4px;
  font-size: 1.4rem;
}

nav ul li a:hover {
  text-decoration: underline;
}

nav ul li span.active {
  color: #ff4040;
  font-size: 22.4px;
  font-size: 1.4rem;
  font-weight: 500;
}

.news ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: calc(100% + 4rem);
  margin-left: -32px;
  margin-left: -2rem;
}

@media (min-width: 640px) {
  .news ul {
    width: auto;
    margin: 0;
  }
}

.news li.news-teaser {
  border-bottom: 1px solid #f2f2f2;
}

.news a {
  position: relative;
  display: block;
  padding: 48px 80px 48px 48px;
  padding: 3rem 5rem 3rem 3rem;
  background: #ffffff;
}

.news a:hover {
  background: #f8f8f8;
}

@media (max-width: 640px) {
  .news a {
    padding-left: 2rem;
  }
}

.news .image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 144px;
  width: 9rem;
  background-size: cover;
  background-position: center center;
}

@media (min-width: 640px) {
  .news .image {
    width: 19rem;
  }
}

.news .image + .content {
  padding-left: 144px;
  padding-left: 9rem;
}

@media (min-width: 640px) {
  .news .image + .content {
    padding-left: 19rem;
  }
}

.news h3 {
  margin-bottom: 0;
}

@media (max-width: 640px) {
  .news h3 {
    font-size: 1.4rem;
    font-weight: 300;
  }
}

.news p {
  margin-top: 16px;
  margin-top: 1rem;
}

@media (max-width: 640px) {
  .news p {
    display: none !important;
  }
}

.news .chevron-right {
  position: absolute;
  top: 50%;
  right: 32px;
  right: 2rem;
  width: 16px;
  width: 1rem;
  opacity: .2;
  transform: translateY(-50%);
}

.teaser {
  display: block;
  position: relative;
  width: 100%;
  max-width: 640px;
  max-width: 40rem;
  color: #000000;
  background-color: #ffffff;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  padding: 288px 48px 96px 48px;
  padding: 18rem 3rem 6rem 3rem;
  margin: 48px auto;
  margin: 3rem auto;
  text-decoration: none;
  transition: box-shadow 0.25s ease;
}

@media screen and (min-width: 640px) {
  .teaser {
    max-width: none;
  }
}

.teaser:hover {
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
}

.teaser .image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 288px;
  height: 18rem;
  background-color: #ff4040;
  background-size: cover;
  background-position: center center;
  overflow: hidden;
}

.teaser .content {
  padding: 48px 0;
  padding: 3rem 0;
}

.teaser .more-link {
  position: absolute;
  bottom: 0;
  width: calc(100% - 6rem);
  text-align: center;
  color: #ff4040;
  font-weight: 500;
  font-size: 22.4px;
  font-size: 1.4rem;
  padding: 32px 0;
  padding: 2rem 0;
  border-top: 1px solid #f2f2f2;
}

@media screen and (min-width: 640px) {
  body.home .hp-teasers {
    display: flex;
    align-items: stretch;
  }
  body.home .hp-teasers .teaser {
    width: calc(50% - 1.5rem);
  }
  body.home .hp-teasers .teaser:first-child {
    margin-right: 3rem;
  }
}
